/**
 * #.# Styles
 *
 * CSS for both Frontend+Backend.
 */

.sptcrb__abouttheauthor__block__wrapper {
	box-sizing: border-box !important;
	background: #f3f3f3 !important;
	border-radius: 3px !important;
	font-family: "lato", lato, helvetica, "helvetica neue" !important;
	display: flex !important;
	padding: 15px !important;
	justify-content: center !important;
}

.sptcrb__abouttheauthor__block__left {
	min-width: 150px !important;
}

.sptcrb__abouttheauthor__block__left__image {
	border-radius: 50% !important;
	height: 150px !important;
	width: 150px !important;
	overflow: hidden !important;
	background: url("base_avatar.png");
	background-repeat: no-repeat !important;
	background-size: cover !important;
	background-position: top center !important;
}

.sptcrb__abouttheauthor__block__left__some__wrapper {
	display: flex !important;
	align-items: center !important;
	justify-content: center !important;
}

.sptcrb__abouttheauthor__block__right {
	padding-left: 15px !important;
	width: 100% !important;
	overflow: hidden !important;
}

.sptcrb__abouttheauthor__block__right > span {
	font-size: 20px !important;
	font-weight: bold !important;
	font-family: "lato", lato, helvetica, "helvetica neue" !important;
	color: #333 !important;
	line-height: 140% !important;
	margin: 0 !important;
}

.sptcrb__abouttheauthor__block__right p {
	font-size: 15px !important;
	font-weight: normal !important;
	font-family: "lato", lato, helvetica, "helvetica neue" !important;
	color: #6d6d6d !important;
	line-height: 140% !important;
	margin: 10px 0 0 !important;
}

.sptcrb__abouttheauthor__block__left__some__icon__wrapper {
	width: 30px !important;
	height: 30px !important;
	color: #fff !important;
	background: #333 !important;
	margin: 10px 5px 0 5px !important;
	border-radius: 50% !important;
	text-align: center !important;
	padding-top: 6px !important;
	box-sizing: border-box !important;
	font-size: 14px !important;
	line-height: 140% !important;
}

.sptcrb__abouttheauthor__block__left__some__icon__wrapper i {
	font-size: 14px !important;
}

.sptcrb__abouttheauthor__block__left__some__icon__linkedin {
	background: #0077b5 !important;
	color: #fff !important;
}

.sptcrb__abouttheauthor__block__left__some__icon__facebook {
	background: #4267b2 !important;
	color: #fff !important;
}

.sptcrb__abouttheauthor__block__left__some__icon__twitter {
	background: #1da1f2 !important;
	color: #fff !important;
}

@media screen and (max-width: 800px) {
	.sptcrb__abouttheauthor__block__wrapper {
		display: block !important;
	}
	.sptcrb__abouttheauthor__block__left,
	.sptcrb__abouttheauthor__block__right {
		width: 100% !important;
	}
	.sptcrb__abouttheauthor__block__left {
		text-align: center !important;
	}
	.sptcrb__abouttheauthor__block__left__image {
		margin: auto !important;
	}
}
